const NEWS = [
    { id: 1, category: "", tit: ",     ", date: "2022.08.22", link: "", src: "/assets/images/main_news01.jpg" },
    { id: 2, category: "", tit: " 2030  Top 5 ", date: "2022.07.14", link: "", src: "/assets/images/main_news02.jpg" },
    { id: 3, category: "", tit: "2022 6 ", date: "20226", link: "", src: "/assets/images/main_news03.jpg" },
]

const News = () => {
    return (
        <section className="News csc" >
            <h2> </h2>
            <p>  .</p>
            <div className="inner">
                {
                    NEWS.map(news => {
                        return (
                            <figure key={news.id}>
                                <a href={news.link}></a>
                                <div className="box">
                                    <img src={process.env.PUBLIC_URL + news.src} alt={news.title} />
                                </div>
                                <strong>{news.category}</strong>
                                <div className="title">{news.tit}</div>
                                <span>{news.date}</span>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}

export default News;