import { useState } from "react"

const SERVICE = [
    {
        id: 1, title: " ",
        sub: [
            { title: " ", link: "#!" },
            { title: "  ", link: "#!" },
            { title: "  ", link: "#!" },
        ]
    },
    {
        id: 2, title: "",
        sub: [
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },

        ]
    },
    {
        id: 3, title: "",
        sub: [
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },
            { title: "", link: "#!" },

        ]
    },
]

const ServiceLink = () => {
    const [slink, setLink] = useState(0);
    return (
        <ul className="ServiceLink">
            {
                SERVICE.map((link, idx) => {
                    return (
                        <li key={link.id}>
                            <div className="title">{link.title}</div>
                            <ul className={"subLInk" + (slink === idx ? 'on' : '')}>

                                {
                                    link.sub.map((sub, idx) => {
                                        return (
                                            <li>{sub.title}</li>
                                        )
                                    })
                                }
                            </ul>

                        </li>
                    )
                })
            }
        </ul >
    )
}

export default ServiceLink;